"use client";

import React, { useState } from "react";
import { useSmartMeter } from "@/hooks/useSmartMeter";

export function MobileLocalPeakAlerts() {
  const { data, error, connecting, connection } = useSmartMeter({
    protocol: "zigbee",
    autoConnect: true,
    pollingInterval: 5000
  });

  const [alertAcknowledged, setAlertAcknowledged] = useState(false);
  const [snoozedUntil, setSnoozedUntil] = useState<Date | null>(null);

  const handleAcknowledge = () => {
    setAlertAcknowledged(true);
    setTimeout(() => setAlertAcknowledged(false), 300000); // Reset after 5 minutes
  };

  const handleSnooze = () => {
    const snoozeTime = new Date();
    snoozeTime.setMinutes(snoozeTime.getMinutes() + 30);
    setSnoozedUntil(snoozeTime);
    setTimeout(() => setSnoozedUntil(null), 1800000); // 30 minutes
  };

  const getPeakEndTime = () => {
    const now = new Date();
    const hour = now.getHours();
    
    if (hour >= 14 && hour < 18) {
      return "6:00 PM";
    } else if (hour >= 19 && hour < 21) {
      return "9:00 PM";
    }
    return "Unknown";
  };

  const getNextPeakPeriod = () => {
    const tomorrow = new Date();
    tomorrow.setDate(tomorrow.getDate() + 1);
    return "Tomorrow 2:00 PM - 6:00 PM";
  };

  const getSavingsRecommendation = () => {
    if (!data) return null;
    
    const highUsageAppliances = data.appliances.filter(app => app.usage > 1.0);
    if (highUsageAppliances.length === 0) return null;
    
    const appliance = highUsageAppliances[0];
    const savings = (appliance.usage * 0.13 * 4).toFixed(2); // Peak rate difference * hours
    
    return {
      appliance: appliance.name,
      savings: parseFloat(savings)
    };
  };

  const isCurrentlySnoozed = snoozedUntil && new Date() < snoozedUntil;
  const recommendation = getSavingsRecommendation();

  return (
    <div className="bg-white rounded-lg p-4 shadow-sm border border-gray-100">
      <div className="flex justify-between items-center mb-3">
        <h2 className="text-lg font-semibold text-black">Peak Alerts</h2>
        {connection && (
          <div className="flex items-center space-x-2">
            <div className={`w-2 h-2 rounded-full ${connection.connected ? 'bg-green-500' : 'bg-red-500'}`}></div>
            <span className="text-xs text-gray-500">Live</span>
          </div>
        )}
      </div>
      
      {connecting && (
        <div className="text-center py-4">
          <div className="inline-block animate-spin rounded-full h-4 w-4 border-b-2 border-black"></div>
          <p className="text-sm text-gray-500 mt-2">Loading peak data...</p>
        </div>
      )}
      
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-3 mb-4">
          <p className="text-sm text-red-600">Unable to load peak alerts</p>
          <p className="text-xs text-red-500 mt-1">{error}</p>
        </div>
      )}
      
      {!connecting && !error && data && (
        <>
          {/* Current Status */}
          {data.peakPeriodActive ? (
            <div className={`mb-4 p-3 rounded-lg border ${
              alertAcknowledged ? 'bg-gray-50 border-gray-200' : 'bg-orange-50 border-orange-200'
            }`}>
              <div className="flex items-center justify-between mb-2">
                <span className={`text-sm font-medium ${
                  alertAcknowledged ? 'text-gray-600' : 'text-orange-800'
                }`}>
                  {alertAcknowledged ? 'Peak Period (Acknowledged)' : 'Peak Period Active'}
                </span>
                {!alertAcknowledged && (
                  <div className="w-2 h-2 bg-orange-500 rounded-full animate-pulse"></div>
                )}
              </div>
              <p className={`text-sm ${
                alertAcknowledged ? 'text-gray-600' : 'text-orange-700'
              }`}>
                High demand period until {getPeakEndTime()}
              </p>
              <div className="mt-2 text-xs text-gray-600">
                Grid Demand: <span className="font-medium capitalize">{data.gridDemand}</span> â€¢ 
                Current Usage: <span className="font-medium">{data.currentUsage.toFixed(1)} kW</span>
              </div>
            </div>
          ) : (
            <div className="mb-4 p-3 bg-green-50 border border-green-200 rounded-lg">
              <div className="flex items-center justify-between mb-2">
                <span className="text-sm font-medium text-green-800">Off-Peak Period</span>
                <div className="w-2 h-2 bg-green-500 rounded-full"></div>
              </div>
              <p className="text-sm text-green-700">
                Standard rates active â€¢ Grid demand is {data.gridDemand}
              </p>
            </div>
          )}

          {/* Alert Message */}
          {data.peakPeriodActive && recommendation && !isCurrentlySnoozed && !alertAcknowledged && (
            <div className="bg-gray-50 p-3 rounded-lg mb-4">
              <p className="text-sm text-black font-medium mb-1">
                ðŸ’¡ Turn off {recommendation.appliance.toLowerCase()} now to save ${recommendation.savings.toFixed(2)}
              </p>
              <p className="text-xs text-gray-600">
                Reduce strain on the grid and lower your bill during peak hours
              </p>
            </div>
          )}

          {/* Snoozed Message */}
          {isCurrentlySnoozed && (
            <div className="bg-blue-50 border border-blue-200 rounded-lg p-3 mb-4">
              <p className="text-sm text-blue-800 font-medium">Alerts Snoozed</p>
              <p className="text-xs text-blue-600 mt-1">
                Notifications paused until {snoozedUntil?.toLocaleTimeString()}
              </p>
            </div>
          )}

          {/* Quick Actions */}
          {data.peakPeriodActive && !alertAcknowledged && !isCurrentlySnoozed && (
            <div className="space-y-2 mb-4">
              <button 
                onClick={handleAcknowledge}
                className="w-full bg-black text-white py-2 px-4 rounded-lg text-sm font-medium hover:bg-gray-800 transition-colors"
              >
                Acknowledge Alert
              </button>
              <button 
                onClick={handleSnooze}
                className="w-full border border-gray-300 text-black py-2 px-4 rounded-lg text-sm font-medium hover:bg-gray-50 transition-colors"
              >
                Snooze for 30 min
              </button>
            </div>
          )}

          {/* Usage Insights */}
          <div className="bg-gray-50 p-3 rounded-lg mb-4">
            <h4 className="text-sm font-medium text-black mb-2">Current Usage Breakdown</h4>
            <div className="space-y-1">
              {data.appliances.slice(0, 2).map((appliance, index) => (
                <div key={index} className="flex justify-between text-xs">
                  <span className="text-gray-600">{appliance.name}</span>
                  <span className="text-black font-medium">
                    {appliance.usage.toFixed(1)} kW â€¢ ${appliance.cost.toFixed(2)}/day
                  </span>
                </div>
              ))}
            </div>
          </div>

          {/* Next Peak */}
          <div className="pt-3 border-t border-gray-100">
            <p className="text-xs text-gray-600">
              Next peak period: {getNextPeakPeriod()}
            </p>
            {data.peakPeriodActive && (
              <p className="text-xs text-gray-500 mt-1">
                Peak rates: $0.35/kWh â€¢ Off-peak: $0.22/kWh
              </p>
            )}
          </div>
        </>
      )}
    </div>
  );
}
