"use client";

import React from "react";
import { useSmartMeter } from "@/hooks/useSmartMeter";

export function MobileRealTimeDashboard() {
  const { data, error, loading, connecting, connection, lastUpdated, refresh } = useSmartMeter({
    protocol: "zigbee",
    autoConnect: true,
    pollingInterval: 5000
  });

  const getUsagePercentage = (usage: number) => {
    // Calculate percentage based on typical household max usage (6kW)
    return Math.min((usage / 6) * 100, 100);
  };

  const getUsageColor = (usage: number) => {
    if (usage > 4) return "bg-red-500";
    if (usage > 2.5) return "bg-yellow-500";
    return "bg-green-500";
  };

  return (
    <div className="bg-white rounded-lg p-4 shadow-sm border border-gray-100">
      <div className="flex justify-between items-center mb-3">
        <h2 className="text-lg font-semibold text-black">Energy Dashboard</h2>
        {connection && (
          <div className="flex items-center space-x-2">
            <div className={`w-2 h-2 rounded-full ${connection.connected ? 'bg-green-500' : 'bg-red-500'}`}></div>
            <span className="text-xs text-gray-500">
              {connection.protocol.toUpperCase()}
            </span>
          </div>
        )}
      </div>
      
      {connecting && (
        <div className="text-center py-4">
          <div className="inline-block animate-spin rounded-full h-6 w-6 border-b-2 border-black"></div>
          <p className="text-sm text-gray-500 mt-2">Connecting to Smart Meter...</p>
        </div>
      )}
      
      {error && !connecting && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-3 mb-4">
          <p className="text-sm text-red-600 font-medium">Connection Error</p>
          <p className="text-xs text-red-500 mt-1">{error}</p>
          <button 
            onClick={refresh}
            className="text-xs text-red-600 underline mt-2 hover:text-red-800"
          >
            Retry Connection
          </button>
        </div>
      )}
      
      {!connecting && !error && data && (
        <>
          {/* Current Usage */}
          <div className="mb-4 p-3 bg-gray-50 rounded-lg">
            <div className="flex justify-between items-center mb-2">
              <span className="text-sm text-gray-600">Current Usage</span>
              <div className="text-right">
                <span className="text-lg font-bold text-black">
                  {data.currentUsage.toFixed(1)} kW
                </span>
                {data.peakPeriodActive && (
                  <div className="text-xs text-orange-600 font-medium">Peak Period</div>
                )}
              </div>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-2">
              <div 
                className={`h-2 rounded-full transition-all duration-500 ${getUsageColor(data.currentUsage)}`}
                style={{ width: `${getUsagePercentage(data.currentUsage)}%` }}
              ></div>
            </div>
            <div className="flex justify-between text-xs text-gray-500 mt-1">
              <span>Grid Demand: {data.gridDemand}</span>
              <span>{getUsagePercentage(data.currentUsage).toFixed(0)}% of max</span>
            </div>
          </div>

          {/* Quick Stats */}
          <div className="grid grid-cols-2 gap-3">
            <div className="bg-gray-50 p-3 rounded-lg text-center">
              <div className="text-lg font-bold text-black">
                ${data.todaysCost.toFixed(2)}
              </div>
              <div className="text-xs text-gray-600">Today's Cost</div>
              {data.peakPeriodActive && (
                <div className="text-xs text-orange-600 mt-1">Peak Rate Active</div>
              )}
            </div>
            <div className="bg-gray-50 p-3 rounded-lg text-center">
              <div className="text-lg font-bold text-black">
                {data.co2Emissions.toFixed(1)} kg
              </div>
              <div className="text-xs text-gray-600">COâ‚‚ Today</div>
              <div className="text-xs text-gray-500 mt-1">
                {(data.co2Emissions / data.currentUsage).toFixed(1)} kg/kWh
              </div>
            </div>
          </div>

          {/* Top Appliances */}
          <div className="mt-4">
            <h3 className="text-sm font-medium mb-2 text-black">Top Energy Users</h3>
            <div className="space-y-2">
              {data.appliances.map((appliance, index) => (
                <div key={index} className="flex justify-between items-center text-sm">
                  <div className="flex items-center space-x-2">
                    <span className="text-gray-700">{appliance.name}</span>
                    <span className={`text-xs px-1 py-0.5 rounded ${
                      appliance.efficiency === 'high' ? 'bg-green-100 text-green-700' :
                      appliance.efficiency === 'medium' ? 'bg-yellow-100 text-yellow-700' :
                      'bg-red-100 text-red-700'
                    }`}>
                      {appliance.efficiency}
                    </span>
                  </div>
                  <div className="text-right">
                    <span className="font-medium text-black">
                      {appliance.usage.toFixed(1)} kW
                    </span>
                    <div className="text-xs text-gray-500">
                      ${appliance.cost.toFixed(2)}/day
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Last Updated */}
          {lastUpdated && (
            <div className="mt-4 pt-3 border-t border-gray-100">
              <div className="flex justify-between items-center">
                <p className="text-xs text-gray-500">
                  Last updated: {lastUpdated.toLocaleTimeString()}
                </p>
                <button 
                  onClick={refresh}
                  className="text-xs text-gray-600 underline hover:text-gray-800"
                >
                  Refresh
                </button>
              </div>
            </div>
          )}
        </>
      )}
    </div>
  );
}
