"use client";

import React, { useState } from "react";
import { useSmartMeter } from "@/hooks/useSmartMeter";
import { SmartMeterProtocol, getAvailableProtocols, validateConfig } from "@/lib/smartMeter";

export function SmartMeterSettings() {
  const { connection, connecting, error, connect, disconnect } = useSmartMeter({
    autoConnect: false
  });

  const [selectedProtocol, setSelectedProtocol] = useState<SmartMeterProtocol>("zigbee");
  const [deviceAddress, setDeviceAddress] = useState("");
  const [port, setPort] = useState("");
  const [baudRate, setBaudRate] = useState("115200");
  const [encryptionKey, setEncryptionKey] = useState("");
  const [validationErrors, setValidationErrors] = useState<string[]>([]);

  const handleConnect = async () => {
    const config = {
      protocol: selectedProtocol,
      deviceAddress: deviceAddress || undefined,
      port: port || undefined,
      baudRate: baudRate ? parseInt(baudRate) : undefined,
      encryptionKey: encryptionKey || undefined
    };

    const errors = validateConfig(config);
    if (errors.length > 0) {
      setValidationErrors(errors);
      return;
    }

    setValidationErrors([]);
    await connect(config);
  };

  const handleDisconnect = async () => {
    await disconnect();
    setValidationErrors([]);
  };

  const getProtocolDescription = (protocol: SmartMeterProtocol) => {
    switch (protocol) {
      case "zigbee":
        return "Wireless mesh network protocol for smart home devices";
      case "p1":
        return "Direct serial connection to smart meter P1 port";
      case "wireless":
        return "WiFi or other wireless communication protocols";
      default:
        return "";
    }
  };

  const getProtocolRequirements = (protocol: SmartMeterProtocol) => {
    switch (protocol) {
      case "zigbee":
        return ["Device Address", "Encryption Key (optional)"];
      case "p1":
        return ["Serial Port", "Baud Rate"];
      case "wireless":
        return ["Device Address (optional)"];
      default:
        return [];
    }
  };

  return (
    <div className="bg-white rounded-lg p-4 shadow-sm border border-gray-100">
      <h2 className="text-lg font-semibold mb-3 text-black">Smart Meter Settings</h2>
      
      {/* Connection Status */}
      <div className="mb-4 p-3 bg-gray-50 rounded-lg">
        <div className="flex items-center justify-between mb-2">
          <span className="text-sm font-medium text-black">Connection Status</span>
          <div className="flex items-center space-x-2">
            <div className={`w-2 h-2 rounded-full ${
              connection?.connected ? 'bg-green-500' : 'bg-red-500'
            }`}></div>
            <span className="text-xs text-gray-600">
              {connection?.connected ? 'Connected' : 'Disconnected'}
            </span>
          </div>
        </div>
        {connection?.connected && (
          <div className="text-xs text-gray-600">
            <p>Protocol: {connection.protocol.toUpperCase()}</p>
            <p>Device ID: {connection.deviceId}</p>
            {connection.signalStrength && (
              <p>Signal Strength: {connection.signalStrength}%</p>
            )}
          </div>
        )}
      </div>

      {/* Protocol Selection */}
      <div className="mb-4">
        <label className="block text-sm font-medium text-black mb-2">
          Communication Protocol
        </label>
        <select
          value={selectedProtocol}
          onChange={(e) => setSelectedProtocol(e.target.value as SmartMeterProtocol)}
          disabled={connection?.connected}
          className="w-full p-2 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-black focus:border-transparent disabled:bg-gray-100 disabled:cursor-not-allowed"
        >
          {getAvailableProtocols().map((protocol) => (
            <option key={protocol} value={protocol}>
              {protocol.toUpperCase()} - {getProtocolDescription(protocol)}
            </option>
          ))}
        </select>
      </div>

      {/* Protocol-specific Configuration */}
      <div className="mb-4 space-y-3">
        <h3 className="text-sm font-medium text-black">Configuration</h3>
        
        {selectedProtocol === "zigbee" && (
          <>
            <div>
              <label className="block text-xs font-medium text-gray-700 mb-1">
                Device Address *
              </label>
              <input
                type="text"
                value={deviceAddress}
                onChange={(e) => setDeviceAddress(e.target.value)}
                placeholder="0x001234567890ABCD"
                disabled={connection?.connected}
                className="w-full p-2 border border-gray-300 rounded text-xs focus:ring-2 focus:ring-black focus:border-transparent disabled:bg-gray-100"
              />
            </div>
            <div>
              <label className="block text-xs font-medium text-gray-700 mb-1">
                Encryption Key (Optional)
              </label>
              <input
                type="password"
                value={encryptionKey}
                onChange={(e) => setEncryptionKey(e.target.value)}
                placeholder="Enter encryption key"
                disabled={connection?.connected}
                className="w-full p-2 border border-gray-300 rounded text-xs focus:ring-2 focus:ring-black focus:border-transparent disabled:bg-gray-100"
              />
            </div>
          </>
        )}

        {selectedProtocol === "p1" && (
          <>
            <div>
              <label className="block text-xs font-medium text-gray-700 mb-1">
                Serial Port *
              </label>
              <input
                type="text"
                value={port}
                onChange={(e) => setPort(e.target.value)}
                placeholder="/dev/ttyUSB0"
                disabled={connection?.connected}
                className="w-full p-2 border border-gray-300 rounded text-xs focus:ring-2 focus:ring-black focus:border-transparent disabled:bg-gray-100"
              />
            </div>
            <div>
              <label className="block text-xs font-medium text-gray-700 mb-1">
                Baud Rate
              </label>
              <select
                value={baudRate}
                onChange={(e) => setBaudRate(e.target.value)}
                disabled={connection?.connected}
                className="w-full p-2 border border-gray-300 rounded text-xs focus:ring-2 focus:ring-black focus:border-transparent disabled:bg-gray-100"
              >
                <option value="9600">9600</option>
                <option value="115200">115200</option>
                <option value="230400">230400</option>
              </select>
            </div>
          </>
        )}

        {selectedProtocol === "wireless" && (
          <div>
            <label className="block text-xs font-medium text-gray-700 mb-1">
              Device Address (Optional)
            </label>
            <input
              type="text"
              value={deviceAddress}
              onChange={(e) => setDeviceAddress(e.target.value)}
              placeholder="192.168.1.100 or device hostname"
              disabled={connection?.connected}
              className="w-full p-2 border border-gray-300 rounded text-xs focus:ring-2 focus:ring-black focus:border-transparent disabled:bg-gray-100"
            />
          </div>
        )}
      </div>

      {/* Requirements Info */}
      <div className="mb-4 p-3 bg-blue-50 border border-blue-200 rounded-lg">
        <h4 className="text-xs font-medium text-blue-800 mb-1">
          {selectedProtocol.toUpperCase()} Requirements:
        </h4>
        <ul className="text-xs text-blue-700 space-y-1">
          {getProtocolRequirements(selectedProtocol).map((req, index) => (
            <li key={index}>â€¢ {req}</li>
          ))}
        </ul>
      </div>

      {/* Validation Errors */}
      {validationErrors.length > 0 && (
        <div className="mb-4 p-3 bg-red-50 border border-red-200 rounded-lg">
          <h4 className="text-xs font-medium text-red-800 mb-1">Configuration Errors:</h4>
          <ul className="text-xs text-red-700 space-y-1">
            {validationErrors.map((error, index) => (
              <li key={index}>â€¢ {error}</li>
            ))}
          </ul>
        </div>
      )}

      {/* Connection Error */}
      {error && (
        <div className="mb-4 p-3 bg-red-50 border border-red-200 rounded-lg">
          <p className="text-xs text-red-600 font-medium">Connection Error</p>
          <p className="text-xs text-red-500 mt-1">{error}</p>
        </div>
      )}

      {/* Action Buttons */}
      <div className="space-y-2">
        {!connection?.connected ? (
          <button
            onClick={handleConnect}
            disabled={connecting}
            className="w-full bg-black text-white py-2 px-4 rounded-lg text-sm font-medium hover:bg-gray-800 transition-colors disabled:bg-gray-400 disabled:cursor-not-allowed"
          >
            {connecting ? "Connecting..." : "Connect to Smart Meter"}
          </button>
        ) : (
          <button
            onClick={handleDisconnect}
            className="w-full border border-gray-300 text-black py-2 px-4 rounded-lg text-sm font-medium hover:bg-gray-50 transition-colors"
          >
            Disconnect
          </button>
        )}
      </div>

      {/* Help Text */}
      <div className="mt-4 pt-3 border-t border-gray-100">
        <p className="text-xs text-gray-500">
          Configure your smart meter connection to receive real-time energy data. 
          Ensure your smart meter supports the selected protocol and is properly configured.
        </p>
      </div>
    </div>
  );
}
