"use client";

import { useState, useEffect, useCallback, useRef } from "react";
import { 
  connectSmartMeter, 
  getRealTimeData, 
  disconnectSmartMeter,
  SmartMeterProtocol, 
  SmartMeterConnection, 
  RealTimeData,
  SmartMeterConfig
} from "@/lib/smartMeter";

export interface UseSmartMeterOptions {
  protocol?: SmartMeterProtocol;
  autoConnect?: boolean;
  pollingInterval?: number; // in milliseconds
  maxRetries?: number;
}

export interface UseSmartMeterReturn {
  connection: SmartMeterConnection | null;
  data: RealTimeData | null;
  error: string | null;
  loading: boolean;
  connecting: boolean;
  lastUpdated: Date | null;
  connect: (config?: SmartMeterConfig) => Promise<void>;
  disconnect: () => Promise<void>;
  refresh: () => Promise<void>;
  clearError: () => void;
}

export function useSmartMeter(options: UseSmartMeterOptions = {}): UseSmartMeterReturn {
  const {
    protocol = "zigbee",
    autoConnect = true,
    pollingInterval = 5000,
    maxRetries = 3
  } = options;

  const [connection, setConnection] = useState<SmartMeterConnection | null>(null);
  const [data, setData] = useState<RealTimeData | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [connecting, setConnecting] = useState<boolean>(false);
  const [lastUpdated, setLastUpdated] = useState<Date | null>(null);

  const intervalRef = useRef<NodeJS.Timeout | null>(null);
  const retryCountRef = useRef<number>(0);
  const mountedRef = useRef<boolean>(true);

  // Connect to smart meter
  const connect = useCallback(async (config?: SmartMeterConfig) => {
    if (connecting) return;
    
    setConnecting(true);
    setError(null);
    
    const connectionConfig: SmartMeterConfig = config || {
      protocol,
      deviceAddress: protocol === "zigbee" ? "0x001234567890ABCD" : undefined,
      port: protocol === "p1" ? "/dev/ttyUSB0" : undefined,
      baudRate: protocol === "p1" ? 115200 : undefined
    };

    try {
      const newConnection = await connectSmartMeter(connectionConfig);
      
      if (!mountedRef.current) return;
      
      setConnection(newConnection);
      retryCountRef.current = 0;
      
      // Fetch initial data
      await fetchData(newConnection);
      
    } catch (err) {
      if (!mountedRef.current) return;
      
      const errorMessage = err instanceof Error ? err.message : "Connection failed";
      setError(errorMessage);
      
      // Retry logic
      if (retryCountRef.current < maxRetries) {
        retryCountRef.current++;
        setTimeout(() => {
          if (mountedRef.current) {
            connect(connectionConfig);
          }
        }, 2000 * retryCountRef.current); // Exponential backoff
      }
    } finally {
      if (mountedRef.current) {
        setConnecting(false);
      }
    }
  }, [connecting, protocol, maxRetries]);

  // Disconnect from smart meter
  const disconnect = useCallback(async () => {
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
      intervalRef.current = null;
    }
    
    if (connection) {
      try {
        await disconnectSmartMeter(connection);
      } catch (err) {
        console.warn("Error during disconnect:", err);
      }
    }
    
    setConnection(null);
    setData(null);
    setError(null);
    setLastUpdated(null);
  }, [connection]);

  // Fetch data from smart meter
  const fetchData = useCallback(async (conn?: SmartMeterConnection) => {
    const activeConnection = conn || connection;
    if (!activeConnection || !activeConnection.connected) return;

    setLoading(true);
    setError(null);

    try {
      const newData = await getRealTimeData(activeConnection);
      
      if (!mountedRef.current) return;
      
      setData(newData);
      setLastUpdated(new Date());
      retryCountRef.current = 0; // Reset retry count on successful fetch
      
    } catch (err) {
      if (!mountedRef.current) return;
      
      const errorMessage = err instanceof Error ? err.message : "Data fetch failed";
      setError(errorMessage);
      
      // If it's a connection error, try to reconnect
      if (errorMessage.includes("not connected") && retryCountRef.current < maxRetries) {
        retryCountRef.current++;
        setTimeout(() => {
          if (mountedRef.current) {
            connect();
          }
        }, 1000);
      }
    } finally {
      if (mountedRef.current) {
        setLoading(false);
      }
    }
  }, [connection, connect, maxRetries]);

  // Refresh data manually
  const refresh = useCallback(async () => {
    await fetchData();
  }, [fetchData]);

  // Clear error
  const clearError = useCallback(() => {
    setError(null);
  }, []);

  // Auto-connect on mount
  useEffect(() => {
    if (autoConnect) {
      connect();
    }

    return () => {
      mountedRef.current = false;
    };
  }, [autoConnect, connect]);

  // Set up polling interval
  useEffect(() => {
    if (connection && connection.connected && !intervalRef.current) {
      intervalRef.current = setInterval(() => {
        fetchData();
      }, pollingInterval);
    }

    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }
    };
  }, [connection, fetchData, pollingInterval]);

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      mountedRef.current = false;
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, []);

  return {
    connection,
    data,
    error,
    loading,
    connecting,
    lastUpdated,
    connect,
    disconnect,
    refresh,
    clearError
  };
}
