"use client";

import React, { useState } from "react";
import { MobileRealTimeDashboard } from "@/components/wattwise/MobileRealTimeDashboard";
import { MobileLocalPeakAlerts } from "@/components/wattwise/MobileLocalPeakAlerts";
import { MobileEnergyChallengeMode } from "@/components/wattwise/MobileEnergyChallengeMode";
import { MobileEnergyRewardsWallet } from "@/components/wattwise/MobileEnergyRewardsWallet";
import { MobileSmartOptimizationAssistant } from "@/components/wattwise/MobileSmartOptimizationAssistant";
import { SmartMeterSettings } from "@/components/wattwise/SmartMeterSettings";
import { ErrorBoundary } from "@/components/ui/ErrorBoundary";
import Link from "next/link";

export default function MobilePreview() {
  const [activeTab, setActiveTab] = useState("dashboard");

  const renderContent = () => {
    switch (activeTab) {
      case "dashboard":
        return (
          <ErrorBoundary>
            <MobileRealTimeDashboard />
          </ErrorBoundary>
        );
      case "alerts":
        return (
          <ErrorBoundary>
            <MobileLocalPeakAlerts />
          </ErrorBoundary>
        );
      case "challenges":
        return (
          <ErrorBoundary>
            <MobileEnergyChallengeMode />
          </ErrorBoundary>
        );
      case "wallet":
        return (
          <ErrorBoundary>
            <MobileEnergyRewardsWallet />
          </ErrorBoundary>
        );
      case "assistant":
        return (
          <ErrorBoundary>
            <MobileSmartOptimizationAssistant />
          </ErrorBoundary>
        );
      case "settings":
        return (
          <ErrorBoundary>
            <SmartMeterSettings />
          </ErrorBoundary>
        );
      default:
        return (
          <ErrorBoundary>
            <MobileRealTimeDashboard />
          </ErrorBoundary>
        );
    }
  };

  return (
    <div className="min-h-screen flex flex-col bg-gray-50 max-w-sm mx-auto border-x border-gray-200">
      {/* Mobile Status Bar Simulation */}
      <div className="bg-black text-white text-xs px-4 py-1 flex justify-between items-center">
        <span>9:41</span>
        <span>WattWise</span>
        <span>100%</span>
      </div>

      {/* Header */}
      <header className="bg-white p-4 shadow-sm border-b border-gray-100">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-xl font-bold text-black">WattWise</h1>
            <p className="text-sm text-gray-600">Smart Energy Monitoring</p>
          </div>
          <Link 
            href="/" 
            className="text-sm text-gray-500 underline"
          >
            Back
          </Link>
        </div>
      </header>

      {/* Main Content Area */}
      <main className="flex-1 overflow-y-auto p-4 pb-20">
        <div className="space-y-4">
          {renderContent()}
        </div>
      </main>

      {/* Bottom Navigation */}
      <nav className="bg-white border-t border-gray-200 fixed bottom-0 left-1/2 transform -translate-x-1/2 w-full max-w-sm">
        <div className="flex justify-around py-1">
          <button
            onClick={() => setActiveTab("dashboard")}
            className={`flex flex-col items-center py-1 px-2 text-xs font-medium transition-colors ${
              activeTab === "dashboard"
                ? "text-black"
                : "text-gray-500 hover:text-gray-700"
            }`}
          >
            <div className="w-5 h-5 mb-1 bg-gray-300 rounded"></div>
            Dashboard
          </button>
          <button
            onClick={() => setActiveTab("alerts")}
            className={`flex flex-col items-center py-1 px-2 text-xs font-medium transition-colors ${
              activeTab === "alerts"
                ? "text-black"
                : "text-gray-500 hover:text-gray-700"
            }`}
          >
            <div className="w-5 h-5 mb-1 bg-gray-300 rounded"></div>
            Alerts
          </button>
          <button
            onClick={() => setActiveTab("challenges")}
            className={`flex flex-col items-center py-1 px-2 text-xs font-medium transition-colors ${
              activeTab === "challenges"
                ? "text-black"
                : "text-gray-500 hover:text-gray-700"
            }`}
          >
            <div className="w-5 h-5 mb-1 bg-gray-300 rounded"></div>
            Challenges
          </button>
          <button
            onClick={() => setActiveTab("wallet")}
            className={`flex flex-col items-center py-1 px-2 text-xs font-medium transition-colors ${
              activeTab === "wallet"
                ? "text-black"
                : "text-gray-500 hover:text-gray-700"
            }`}
          >
            <div className="w-5 h-5 mb-1 bg-gray-300 rounded"></div>
            Wallet
          </button>
          <button
            onClick={() => setActiveTab("assistant")}
            className={`flex flex-col items-center py-1 px-2 text-xs font-medium transition-colors ${
              activeTab === "assistant"
                ? "text-black"
                : "text-gray-500 hover:text-gray-700"
            }`}
          >
            <div className="w-5 h-5 mb-1 bg-gray-300 rounded"></div>
            Assistant
          </button>
          <button
            onClick={() => setActiveTab("settings")}
            className={`flex flex-col items-center py-1 px-2 text-xs font-medium transition-colors ${
              activeTab === "settings"
                ? "text-black"
                : "text-gray-500 hover:text-gray-700"
            }`}
          >
            <div className="w-5 h-5 mb-1 bg-gray-300 rounded"></div>
            Settings
          </button>
        </div>
      </nav>
    </div>
  );
}
